/* Offers Page Styles */
.offers-page-content {
  padding: 32px 24px 24px 24px;
  /* background: linear-gradient(135deg, #f8fafc 0%, #e0f2fe 100%); */
  min-height: 100vh;
}

.offers-main-title {
  font-size: 2.2rem;
  font-weight: 800;
  color: #029631;
  margin-bottom: 32px;
  letter-spacing: 0.5px;
  text-align: left;
}

.offers-section {
  margin-bottom: 38px;
}

.offers-section-title {
  font-size: 1.4rem;
  font-weight: 700;
  color: #0e7490;
  margin-bottom: 18px;
  letter-spacing: 0.2px;
}

.offers-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 28px;
}

.offer-card {
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 4px 18px rgba(37,99,235,0.08);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  transition: transform 0.18s, box-shadow 0.18s;
  position: relative;
  border: 2px solid transparent;
}
.offer-card:hover {
  transform: translateY(-6px) scale(1.025);
  box-shadow: 0 8px 32px rgba(37,99,235,0.16);
  border-color: #60faaf;
}
.heig-og{
  height: 220px !important;
}
.offer-img-wrap {
  position: relative;
  width: 100%;
  height: 150px;
  background: #f1f5f9;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.offer-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  transition: transform 0.18s;
}
.offer-card:hover .offer-img {
  transform: scale(1.06);
}

.offer-badge {
  position: absolute;
  top: 14px;
  left: 14px;
  background: #2563eb;
  color: #fff;
  font-size: 1rem;
  font-weight: 700;
  padding: 6px 16px;
  border-radius: 16px;
  box-shadow: 0 2px 8px rgba(37,99,235,0.10);
  z-index: 2;
  letter-spacing: 0.2px;
}
.hot-badge { background: #ef4444; }
.limited-badge { background: #f59e42; }
.recommended-badge { background: #10b981; }

.offer-timer {
  position: absolute;
  bottom: 14px;
  left: 14px;
  background: #fbbf24;
  color: #fff;
  font-size: 0.98rem;
  font-weight: 600;
  padding: 5px 14px;
  border-radius: 14px;
  box-shadow: 0 1px 4px rgba(251,191,36,0.10);
  z-index: 2;
}

.offer-content {
  padding: 18px 20px 22px 20px;
  display: flex;
  flex-direction: column;
  flex: 1;
}
.offer-title {
  margin-top: 2px;
  font-size: 1.18rem;
  font-weight: 700;
  color: #1e293b;
  margin-bottom: 6px;
}
.offer-desc {
  font-size: 1rem;
  color: #64748b;
  margin-bottom: 18px;
  flex: 1;
}
.offer-btn {
  background: linear-gradient(90deg, #228B22, #3CB371);
  color: #fff;
  font-weight: 700;
  font-size: 1.08rem;
  border: none;
  border-radius: 10px;
  padding: 10px 0;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(37,99,235,0.10);
  transition: background 0.18s, box-shadow 0.18s;
}
.offer-btn:hover {
  background:linear-gradient(45deg, #3CB371, #228B22);
  box-shadow: 0 4px 16px rgba(37,99,235,0.18);
}

.animated-green {
  background: linear-gradient(90deg, #10b981 0%, #34d399 100%);
  color: #fff;
  border: none;
  border-radius: 24px;
  padding: 10px 28px;
  font-weight: 700;
  font-size: 1rem;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(16,185,129,0.13);
  transition: background 0.4s, transform 0.2s;
  position: relative;
  overflow: hidden;
  outline: none;
}
.animated-green::after {
  content: "";
  position: absolute;
  left: -75%;
  top: 0;
  width: 50%;
  height: 100%;
  background: rgba(255,255,255,0.18);
  transform: skewX(-20deg);
  transition: left 0.5s;
}
.animated-green:hover {
  background: linear-gradient(90deg, #34d399 0%, #10b981 100%);
  transform: translateY(-2px) scale(1.04);
}
.animated-green:hover::after {
  left: 120%;
}

.offers-slider .offer-card {
  margin: 10px 10px 10px 0px !important; 
  width: calc(100% - 20px);
  display: inline-block;
}
.offers-slider .slick-slide > div {
  padding: 0 10px;
}

.offers-section:hover .limited-btn{
  display: block;
}

.limited-btn {
  display: none;
  position: absolute;
  top: 50%;
  z-index: 2;
  transform: translateY(-50%);
  background: #e5f9f3;
  color: #10b981;
  border: 1px solid #10b981;
  border-radius: 50%;
  width: 38px;
  height: 38px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.3s, color 0.3s;
  box-shadow: 0 2px 8px rgba(16,185,129,0.10);
  outline: none;
}
.limited-btn.left {
  left: -18px;
}
.limited-btn.right {
  right: -18px;
}
.limited-btn:hover {
  background: linear-gradient(90deg, #10b981 0%, #34d399 100%);
  color: #fff;
}

@media (max-width: 900px) {
  .offers-grid {
    grid-template-columns: repeat(auto-fit, minmax(210px, 1fr));
    gap: 18px;
  }
  .offers-page-content {
    padding: 18px 6px 18px 6px;
  }
}

@media (max-width: 600px) {
  .offers-main-title {
    font-size: 1.3rem;
    margin-bottom: 18px;
  }
  .offers-section-title {
    font-size: 1.08rem;
    margin-bottom: 10px;
  }
  .offer-content {
    padding: 12px 10px 14px 10px;
  }
}
